#main-wrap {
    width: 100%;
    overflow: hidden;
    #main {
        width: 100%;
        min-height: 100%;
    }
    #main-content {
        padding-top: 30px;
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        display: block;
        overflow-y: auto;
        .edit {
            padding: 0 20px;
            overflow: auto;
        }
    }
    @media only screen and (max-width: 479px) {
        #main #main-content {
            padding-top: 90px;
        }
    }
    @media only screen and (min-width: 480px) and (max-width: 767px) {
        #main #main-content {
            padding-top: 90px;
        }
    }
    @media only screen and (min-width: 768px) and (max-width: 1023px) {
        #main-content, #footer {
            padding-left: 75px;
        }
    }
    @media only screen and (min-width: 1024px) and (max-width: 1279px) {
        #main-content, #footer {
            padding-left: 250px;
        }
    }
    @media only screen and (min-width: 1280px) {
        #main-content, #footer {
            padding-left: 500px;
        }
    }
}
